<template>
    <div class="add_user">
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="姓名">
                <el-input v-model="form.admin_name" placeholder="请选择姓名"></el-input>
            </el-form-item>
            <el-form-item label="账号">
                <el-input v-model="form.admin_id" placeholder="请选择账号"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input v-model="form.admin_psw" placeholder="请选择密码"></el-input>
            </el-form-item>
            <el-form-item label="手机号码">
                <el-input v-model="form.admin_phone" placeholder="请选择手机号码"></el-input>
            </el-form-item>
            <el-form-item label="权限">
                <el-select v-model="form.admin_jurisdiction" placeholder="请选择权限">
                    <el-option label="超级管理员" value="super_editor"></el-option>
                    <el-option label="管理员" value="admin"></el-option>
                    <el-option label="访问者" value="staff"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="职位">
                <el-select v-model="form.admin_post" placeholder="请选择职位">
                    <el-option label="总经理" value="1"></el-option>
                    <el-option label="客服" value="2"></el-option>
                    <el-option label="员工" value="3"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">立即创建</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                width:"50px",
                form: {
                    admin_name: '',
                    admin_id:"",
                    admin_jurisdiction:"",
                    admin_phone:"",
                    admin_post:"",
                    admin_psw:""
                }
            }
        },
        methods: {
            onSubmit() {
                let that = this;
                let data = {
                    admin_name: that.form.admin_name,
                    admin_id:that.form.admin_id,
                    admin_jurisdiction:that.form.admin_jurisdiction,
                    admin_phone:that.form.admin_phone,
                    admin_post:that.form.admin_post,
                    admin_psw:that.form.admin_psw,
                }
                let url = '/api'+that.$common.addUser;
                this.$http.post(url,data).then((res)=>{
                    console.log(res)
                    if ( res.status == 1 ) {
                        this.$router.push({ path:'/adminUser' });
                        this.$message({
                            type: 'success',
                            message: '添加成功!'
                        });
                    } else if ( res.status == 2 ) {
                        this.$message({
                            type: 'success',
                            message: '添加失败!'
                        });
                    } else if (res.code == 404) {
                        sessionStorage.clear();
                        this.$router.push({ path:'/login' });
                    }
                   
                }).catch(data=>{
                    this.$message({
                        type: 'success',
                        message: '添加失败!'
                    });
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .el-select,
    .el-input {
        width: 500px !important;
    }
</style>



